import { createRouter, createWebHashHistory } from 'vue-router'
import Docs from '../views/docs.vue'
import Balance from '../views/Balance.vue'

const routes = [
  {
    path: '/',
    name: 'Docs',
    component: Docs
  },
  {
    path: '/balance',
    name: 'Balance',
    component: Balance
    }
  
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
